const axios = require("axios");
const AWS = require('aws-sdk');


function filter_answered_question(data, question_ids) {
    const review_question_ids = [];
    for (let question_id of question_ids){
        if (data[question_id]['passed'] === true){
        }
        else{
            if ((data[question_id]['object_bias1'] === '' || data[question_id]['object_bias1'] === undefined || data[question_id]['object_bias1'] === null) &&
                (data[question_id]['object_bias2'] === '' || data[question_id]['object_bias2'] === undefined || data[question_id]['object_bias2'] === null) &&
                (data[question_id]['edit_distance'] === '' || data[question_id]['edit_distance'] === undefined || data[question_id]['edit_distance'] === null) &&
                (data[question_id]['is_sure1'] === '' || data[question_id]['is_sure1'] === undefined || data[question_id]['is_sure1'] === null) &&
                (data[question_id]['is_sure2'] === '' || data[question_id]['is_sure2'] === undefined || data[question_id]['is_sure2'] === null)){
                review_question_ids.push(question_id);
            }
        }
    }
    return review_question_ids;
}


function chooseKfromArray(k, values) {          // k = number_of_data,
                                                // values = all_question_ids
    if (k > values.length){         // 남은게 별로 없으면
        k = values.length;
    }

    const indexArray = [];
    for (let i = 0; i < values.length; i++) {
        indexArray.push(i);
    }
    indexArray.sort(function(){         // random sorting
        return 0.5 - Math.random();
    });

    let ret = [];
    for (let i = 0; i < k; i++) {       //
        let idx = indexArray[i];
        ret.push(values[idx]);
    }
    return ret;
}


async function parseData(data, number_of_data) {
    /* json data structure
        {question_id: {question: "~~~~~",
                       correct: "~~~~~",
                       answered: 0,
                       },
          question_id: {question: "~~~~~",
                       correct: "~~~~~",
                       answered: 0,
                       },
           ...
         }
     */

    let all_question_ids = Object.keys(data);      // question_ids
    const filtered_question_ids = filter_answered_question(data, all_question_ids)
    console.log("Total Remaining validation_data: ", filtered_question_ids.length)

    let selected_question_ids;
    if (number_of_data > 0){
        selected_question_ids = chooseKfromArray(number_of_data, filtered_question_ids);
    }
    else{
        // Or we select all passages
        selected_question_ids=filtered_question_ids;
    }

    // Get (number_of_questions) random questions to show the user.
    let ret_data = [];
    for (let i = 0; i < selected_question_ids.length; i++) {
        let qId = selected_question_ids[i];
        const question_info = data[qId];
        question_info["question_id"] = qId;     // question_info = {question: '~~',
                                                        //                  correct: '~~~',
                                                        //                  question_id: xxx}
        ret_data.push(question_info);
    }
    return ret_data;
}


async function fetchData(number_of_data) {

    try{
        AWS.config.update({
            accessKeyId: 'yourID',
            secretAccessKey: 'yourSecretKey',
            region: 'us-east-1',
            signatureVersion: 'v4'
        })
        const s3 = new AWS.S3()

        const Bucket = 'creation-raw-data-storage'
        const dataKey = 'data/answered_to_val.json'
        const signedUrlExpireSeconds = 60*5
        const dataGETParams = {
            Bucket: Bucket,
            Key: dataKey,
            Expires: signedUrlExpireSeconds,
        }
        const data_GET_url = s3.getSignedUrl('getObject', dataGETParams)
        const data = await axios.get(data_GET_url).then(response=>{
            return response.data
        });

        return await parseData(data, number_of_data);            // return questions
    }
    catch (e) {
        console.log(e);
    }
}


async function writeToS3(answered, bonus_list) {
    // bonus_list = {workerId1 : {assignmentId1: 0000,
    //                          assignmentId2: 1111, ... }
    //                 ...
    //               workerId : {assignmentId: 0000, ...},
    //              }


    // initial setting for pre-signed URL
    AWS.config.update({
        accessKeyId: 'yourID',
        secretAccessKey: 'yourSecretKey',
        region: 'us-east-1',
        signatureVersion: 'v4'
    })
    const s3 = new AWS.S3()

    const Bucket = 'creation-raw-data-storage'
    const dataKey = 'data/answered_to_val.json'

    const signedUrlExpireSeconds = 60*5
    const dataGETParams = {
        Bucket: Bucket,
        Key: dataKey,
        Expires: signedUrlExpireSeconds,
    }
    const dataPUTParams = {
        Bucket: Bucket,
        Key: dataKey,
        Expires: signedUrlExpireSeconds,
        // ContentType: "application/json",
        ACL: 'public-read'
    }

    const data_GET_url = s3.getSignedUrl('getObject', dataGETParams)
    const data_PUT_url = s3.getSignedUrl('putObject', dataPUTParams)
    // console.log("seed_GET_url: ",seed_GET_url)
    // console.log("seed_PUT_url: ", seed_PUT_url)


    // If there is data to save
    if (Object.keys(answered).length !== 0){
        const data = await axios.get(data_GET_url).then(response=>{
        return response.data                // data = {question_id: {question: "~~~~~",
                                            //                             correct: "~~~~~",
                                            //                             answered: 0             },
                                            //                   ... ,
                                            //              }
        });


        let qids = Object.keys(answered);
        // console.log("question_ids: ", qids)
        for (let qid of qids){
            const original = data[qid]
            let current = answered[qid]
            let add_keys = Object.keys(current)
            for (let q of add_keys){
                original[q] = current[q]
            }
        }
        const options = {headers: {'Content-Type': 'application/json'}};
        await axios.put(data_PUT_url, JSON.stringify(data), options)
            .then(function(){
                console.log("Add the validation results (object_bias1, object_bias2 edit_distance, is_sure1, is_sure2, valid) in answered_to_val.json file.")
            })
        }


    if (bonus_list !== false) {
        const bonusKey = 'data/bonus.json'

        const bonusGETParams = {
            Bucket: Bucket,
            Key: bonusKey,
            Expires: signedUrlExpireSeconds,
        }
        const bonusPUTParams = {
            Bucket: Bucket,
            Key: bonusKey,
            Expires: signedUrlExpireSeconds,
            // ContentType: "application/json",
            ACL: 'public-read'
        }

        const bonus_GET_url = s3.getSignedUrl('getObject', bonusGETParams)
        const bonus_PUT_url = s3.getSignedUrl('putObject', bonusPUTParams)

        let workerId = Object.keys(bonus_list)
        let AssignmentId = Object.keys(bonus_list[workerId])
        let bonus = bonus_list[workerId][AssignmentId]

        // console.log("workerId: ", workerId)
        // console.log("AssignmentId: ", AssignmentId)
        // console.log("bonus: ", bonus)

        const save_bonus = await axios.get(bonus_GET_url)
            .then(function (res) {
                let temp = res.data
                if (temp[workerId] === undefined){
                    let initial_dict = {};
                    initial_dict[AssignmentId] = 0
                    temp[workerId] = initial_dict
                }
                temp[workerId][AssignmentId] = bonus
                return temp;
            }).catch(function (err) {
                if (err.response.status === 404) {
                    console.log('Make initial feedback file to save.');
                    return bonus_list;
                }
            });

        let settings = {headers: {'Content-Type': 'application/json', 'x-amz-acl': 'public-read'}};
        await axios.put(bonus_PUT_url, JSON.stringify(save_bonus), settings).then(function () {
            console.log("Save the worker's feedback to S3.")
        })
    }
}


module.exports = {
    parseData,
    chooseKfromArray,
    fetchData,
    writeToS3,
};