<script>
export default {
  name: 'Dataset',
  data() {
    return {
      stop_words:
            ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'l', 'm', 'n', 'o', 'p', 'q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'y', 'z',
              'the', 'for', 'in', 'of', "'s", 'my', 'me', 'you', 'are', 'is', 'were', 'was', 'be', "your", "an", "as", "for", "from",
              "i'm", "you're", 'it', ",", ".", "'ve", "we", "them", "they", "we've", "at", "such", "him", "her", "at", "their", "my", "on",
              "they're", "we'll", "i'll", "you'll", "he'll", "she'll", "it's", "its", "do", "so", "to", "and", "she", "he", "on", "into", "make",
              "with", "when", "why", "how", "what", "while", "can", "about", "take"],

      paint_data0: '',
      paint_data1: '',
      paint_data2: '',
      paint_data00: '',
      paint_data11: '',
      paint_data22: '',
    }
  },

  props: {
    annotation: {
      type: Object,
      required: true,
    },
    index: {
      type: String,
      required: true,
    },
  },


  watch: {
    annotation() {
      this.$emit('input', this.annotation);
    },
  },

  methods: {
    render_radio(value) {
      if (value === '0') {
        return "Yes";
      }
      if (value === '1') {
        return "No";
      }
    },
  },

  mounted: async function() {
    let question;
    let sen1;
    let sen2;

    question = this.annotation.question;
    sen1 = this.annotation.correct;
    sen2 = this.annotation.incorrect;

    let words = question.split(' ');
    let sen1_words = sen1.split(' ');
    let sen2_words = sen2.split(' ');

    let words_low = words.map(sen => sen.toLowerCase())
    let sen1_words_low = sen1_words.map(sen => sen.toLowerCase())
    let sen2_words_low = sen2_words.map(sen => sen.toLowerCase())

    let sen_len = words.length;

    // paint question
    let only_sen1 = [];
    let only_sen2 = [];
    let new_que = '';
    for (let i=0; i<sen_len; i++){
      let last = ''
      if ((i === sen_len-1) && ['.', ',', '?', '!', '"', "'", '~', ';', '>'].includes(words_low[i].slice(-1))){
        last = words_low[i].slice(-1)
        words_low[i] = words_low[i].slice(0, -1)
        words[i] = words[i].slice(0, -1)
      }

      if (sen1.toLowerCase().indexOf(words_low[i]) !== -1 && sen2.toLowerCase().indexOf(words_low[i]) === -1 && !this.stop_words.includes(words_low[i])){
        only_sen1.push(words[i].toLowerCase())
        new_que = new_que.concat('&nbsp;', '<i style="color: red">', words[i], '</i>', last,'&nbsp;')
      }
      else{
        if (sen2.toLowerCase().indexOf(words_low[i]) !== -1 && sen1.toLowerCase().indexOf(words_low[i]) === -1 && !this.stop_words.includes(words_low[i])){
          only_sen2.push(words[i].toLowerCase())
          new_que = new_que.concat('&nbsp;', '<i style="color: blue">', words[i], '</i>', last, '&nbsp;')
        }
        else{
          new_que = new_que.concat(' ', words[i])
        }
      }
    }

    // paint sentence1
    let new_sen1 = '';
    for (let i=0; i < sen1_words_low.length; i++){
      if (only_sen1.length !== 0){
        let cnt = 0;
        for (let j=0; j < only_sen1.length; j++){
          if (sen1_words_low[i].indexOf(only_sen1[j]) !== -1){
            new_sen1 = new_sen1.concat('&nbsp;', '<i style="color: red">', sen1_words[i], '</i>', '&nbsp;')
            break
          }
          else{
            cnt += 1;
          }
          if (cnt === only_sen1.length){
              new_sen1 = new_sen1.concat(' ', sen1_words[i])
          }
        }
      }
      else{
        new_sen1 = new_sen1.concat(' ', sen1_words[i])
      }
    }

    // paint sentence2
    let new_sen2 = '';
    for (let i=0; i < sen2_words_low.length; i++){
      if (only_sen2.length !== 0){
        let cnt = 0;
        for (let j=0; j < only_sen2.length; j++){
          if (sen2_words_low[i].indexOf(only_sen2[j]) !== -1){
            new_sen2 = new_sen2.concat('&nbsp;', '<i style="color: blue">', sen2_words[i], '</i>', '&nbsp;')
            break
          }
          else{
            cnt += 1;
          }
          if (cnt === only_sen2.length){
              new_sen2 = new_sen2.concat(' ', sen2_words[i])
          }
        }
      }
      else{
        new_sen2 = new_sen2.concat(' ', sen2_words[i])
      }
    }

    var dom = document.createElement('div');
    dom.innerHTML = new_que;
    this.paint_data0 = dom.innerHTML;
    dom.innerHTML = new_sen1;
    this.paint_data1 = dom.innerHTML;
    dom.innerHTML = new_sen2;
    this.paint_data2 = dom.innerHTML;



    //////////////////////////////////////////////////////////////////////
    /////////////////////////// for QA DATA 2 ////////////////////////////
    //////////////////////////////////////////////////////////////////////
    question = this.annotation.new_question;
    sen1 = this.annotation.incorrect;
    sen2 = this.annotation.correct;

    words = question.split(' ');
    sen1_words = sen1.split(' ');
    sen2_words = sen2.split(' ');

    words_low = words.map(sen => sen.toLowerCase())
    sen1_words_low = sen1_words.map(sen => sen.toLowerCase())
    sen2_words_low = sen2_words.map(sen => sen.toLowerCase())

    sen_len = words.length;

    // paint question
    only_sen1 = [];
    only_sen2 = [];
    new_que = '';
    for (let i=0; i<sen_len; i++){
      let last = ''
      if ((i === sen_len-1) && ['.', ',', '?', '!', '"', "'", '~', ';', '>'].includes(words_low[i].slice(-1))){
        last = words_low[i].slice(-1)
        words_low[i] = words_low[i].slice(0, -1)
        words[i] = words[i].slice(0, -1)
      }

      if (sen1.toLowerCase().indexOf(words_low[i]) !== -1 && sen2.toLowerCase().indexOf(words_low[i]) === -1 && !this.stop_words.includes(words_low[i])){
        only_sen1.push(words[i].toLowerCase())
        new_que = new_que.concat('&nbsp;', '<i style="color: red">', words[i], '</i>', last,'&nbsp;')
      }
      else{
        if (sen2.toLowerCase().indexOf(words_low[i]) !== -1 && sen1.toLowerCase().indexOf(words_low[i]) === -1 && !this.stop_words.includes(words_low[i])){
          only_sen2.push(words[i].toLowerCase())
          new_que = new_que.concat('&nbsp;', '<i style="color: blue">', words[i], '</i>', last, '&nbsp;')
        }
        else{
          new_que = new_que.concat(' ', words[i])
        }
      }
    }

    // paint sentence1
    new_sen1 = '';
    for (let i=0; i < sen1_words_low.length; i++){
      if (only_sen1.length !== 0){
        let cnt = 0;
        for (let j=0; j < only_sen1.length; j++){
          if (sen1_words_low[i].indexOf(only_sen1[j]) !== -1){
            new_sen1 = new_sen1.concat('&nbsp;', '<i style="color: red">', sen1_words[i], '</i>', '&nbsp;')
            break
          }
          else{
            cnt += 1;
          }
          if (cnt === only_sen1.length){
              new_sen1 = new_sen1.concat(' ', sen1_words[i])
          }
        }
      }
      else{
        new_sen1 = new_sen1.concat(' ', sen1_words[i])
      }
    }

    // paint sentence2
    new_sen2 = '';
    for (let i=0; i < sen2_words_low.length; i++){
      if (only_sen2.length !== 0){
        let cnt = 0;
        for (let j=0; j < only_sen2.length; j++){
          if (sen2_words_low[i].indexOf(only_sen2[j]) !== -1){
            new_sen2 = new_sen2.concat('&nbsp;', '<i style="color: blue">', sen2_words[i], '</i>', '&nbsp;')
            break
          }
          else{
            cnt += 1;
          }
          if (cnt === only_sen2.length){
              new_sen2 = new_sen2.concat(' ', sen2_words[i])
          }
        }
      }
      else{
        new_sen2 = new_sen2.concat(' ', sen2_words[i])
      }
    }


    dom = document.createElement('div');
    dom.innerHTML = new_que;
    this.paint_data00 = dom.innerHTML;
    dom.innerHTML = new_sen1;
    this.paint_data11 = dom.innerHTML;
    dom.innerHTML = new_sen2;
    this.paint_data22 = dom.innerHTML;
  }
}
</script>

