<template>
  <div id="app">
    <div class="container">
      <div id="head" class="row mb-2 mt-2">
        <span class="col" style="text-align: center; font-size: 40px; color: #2c3e50; margin-top: 30px; margin-bottom: 10px;">
          Validation
        </span>
      </div>
      <hr class="mx-auto" style="border: 2px double slategray;">

      <nav>
        <div class="nav nav-pills nav-fill" id="nav-tab" role="tablist">
          <a class="nav-item nav-link"
             v-on:click="switch_tab('guideline')"
             v-bind:class="{
              'active': show_tab_guideline
             }"
          ><span style="font-size: 25px">Guideline</span></a>

          <a class="nav-item nav-link"
             v-on:click="switch_tab('payment')"
             v-bind:class="{
              'active': show_tab_payment
             }"
          ><span style="font-size: 25px">Payment</span></a>

          <a class="nav-item nav-link"
             v-on:click="switch_tab('workspace')"
             v-bind:class="{
              'active': show_tab_workspace
             }"
          ><span style="font-size: 25px">Workspace</span></a>
        </div>
      </nav>

      <div class="container" v-if="show_tab_guideline">
        <div class="card-body" style="text-align: left; margin-left: 10px; margin-top: 10px; margin-bottom: 10px;"><strong>
          Hello. Thank you for your interest in our HIT. This is a simple task to validate the data about Physical Commonsense Question and Answering.
          Before starting the real validation work, we will first introduce the Data Format used in the validation task and the three types of Invalid Data that must be avoided.
          After understanding all the details, you can proceed to the real validation workspace on the next page to check whether the data contains the relative invalid element by checking the Yes or No checkbox.
        </strong></div>
        <Guideline></Guideline>

        <hr class="mx-auto" style="border: 2px double slategray; max-width: 1073px;">
        <button
                  type="button"
                  class="btn btn-info btn-lg"
                  style="float: right; background-color: steelblue; color: white;"
                  onclick="window.scrollTo(0,0)"
                  v-on:click="switch_tab('payment');"
          >Pay plan
        </button>
        <br/><br/><br/><br/>
      </div>

      <div class="container" v-if="show_tab_payment">
        <div>
          <br/><br/>
          <Payment></Payment>
          <br/>
          <div class="row">
            <div class="col-8"></div>
            <div class="col-4">
              <button
                        type="button"
                        class="btn btn-info btn-lg v-btn--right"
                        style="float: right; background-color: steelblue; color: white"
                        onclick="window.scrollTo(0,0)"
                        v-on:click="switch_tab('workspace');"
                >Go to the workspace
              </button>
            </div>
          </div>
        </div>
      </div>


      <div class="container" v-if="show_tab_workspace">
        <div id="data_board" style="margin-left: 10px;" v-if="answered_data.length !== 0 && !loading">
          <div style="font-size: 24px; margin-top: 30px; margin-bottom: 10px;">
            <strong>DATA BOARD</strong>
          </div>
          <div v-for="(item, idx) in this.answered_data"
               v-bind:key="idx"
               style="float: left; margin-left: 3px; margin-right:3px; margin-bottom: 5px;">
            <v-btn v-if="Object.values(item)[0].all && stage !== parseInt(idx)"
                   v-on:click="move_to_num(idx)"
                   style="background-color: lightgrey;">
              {{parseInt(idx)+1}}
            </v-btn>
            <v-btn v-else-if="Object.values(item)[0].all && stage === parseInt(idx)"
                   v-on:click="move_to_num(idx)"
                   style="background-color: lightgrey; border: 3px solid mediumseagreen">
              {{parseInt(idx)+1}}
            </v-btn>
            <v-btn
                v-else-if="!Object.values(item)[0].all && stage !== parseInt(idx)"
                v-on:click="move_to_num(idx)"
                style="background-color: white; ">
              {{parseInt(idx)+1}}
            </v-btn>
            <v-btn
                v-else-if="!Object.values(item)[0].all && stage === parseInt(idx)"
                v-on:click="move_to_num(idx)"
                style="background-color: white; border: 3px solid mediumseagreen">
              {{parseInt(idx)+1}}
            </v-btn>
          </div>

          <div style="font-size: 16px; margin-left: 10px;">
            <div v-if="number_of_data > 15" style="margin-bottom: 10px;">
              <br/><br/><br/><br/>
            </div>
            <div v-else style="margin-bottom: 10px;">
              <br/><br/>
            </div>
            <strong>This board shows the current validation progress.
              The number box with a green border refers to the data displayed in the current workspace.
              A white number box refers to the data that has not yet been validated, and a gray number box refers to the data that has been validated.
              Click on the number box to jump to relative data. For each data, the number box turns grey automatically if all checkboxes are checked.
              Only when all the number boxes have turned gray, you can submit the HIT by clicking the "Submit" button below or get more dataset by clicking the "Get more dataset" button below.
            </strong>
          </div>
        </div>

        <div id="show_data" class="container" style="margin-bottom: 20px;">
          <div>
            <div v-if="answered_data.length===0 && !loading" style="margin-bottom: 130px; margin-top: -70px; text-align: center">
              <br/><br/><br/><br/><br/><br/><br/><br/>
              <h1><strong>
                There is no Data to verify. <br/><br/> Thank you.
              </strong></h1>
            </div>

            <div id="first_loading" v-if="this.loading">
              <br/><br/><br/><br/>
              <div class="loader" style="border: 50px solid #f3f3f3; border-top: 50px solid #5b8bb4; width: 300px; height: 300px; animation: spin 1.5s linear infinite"/><br/>
              <div style="font-size: 25px; text-align: center"><strong>Data Loading... Please wait. <br/>(about 10~30 seconds)</strong></div>
              <br/><br/><br/>
            </div>

            <br/><br/>

            <Dataset
                v-for="(item, key) in answered_data[this.stage]"
                v-bind:key="key"
                v-bind:index="key.toString()"
                v-bind:annotation="item"
                style="margin-bottom: 50px;"
            >
            </Dataset>
          </div>

          <button class="btn btn-5"
                  v-on:click="pre_data"
                  style="float:left; margin-top: -40px;"
          >
            <div style="font-size: 18px; font-family: Helvetica Neue,serif; margin-bottom:7px;">PREVIOUS</div>
            <div style="color: black; font-size: 36px;" class="material-icons">arrow_back_ios</div>
          </button>

          <button class="btn btn-5"
                  v-on:click="next_data"
                  style="float:right; margin-top: -40px;"
          >
            <div style="font-size: 20px; font-family: Helvetica Neue,serif; margin-bottom:7px;">NEXT</div>
            <span style="color: black; font-size: 36px;" class="material-icons">arrow_forward_ios</span>
          </button>

          <br/><br/><br/>

          <ul class="list-group list-group-flush" style="float: left">
            <strong>
              <li class="list-group-item mx-auto"
                  style="text-align: left;"
                  v-bind:class="{
                    'list-group-item-warning': true,
                }"
              >
                <span style="margin-right: 5px; font-size: 16px;">The number of Completed pages (30 data)</span>
                :
                <span style="margin-left: 15px; margin-right: 10px; font-size: 18px;"> {{this.num_complete_page}} </span>
              </li>
              <li class="list-group-item mx-auto"
                  style="text-align: left;"
                  v-bind:class="{
                    'list-group-item-warning': true,
                }"
              >
                <span style="margin-right: 5px; font-size: 16px;">Total Reward : </span>
                :
                <span style="margin-left: 15px; margin-right: 10px; font-size: 18px;">$ {{total_payment}} </span>

                <span
                  class="material-icons"
                  style="font-size: 24px; float: right"
                  data-balloon-pos="right"
                  aria-label="The reward is $2 per 30 data on each page."
              > help_outline
              </span>
              </li>
            </strong>
          </ul>

          <button   type="button"
                    class="btn btn-lg"
                    :disabled="!this.all_answered || this.loading || this.submitting || this.adding || answered_data.length===0"
                    style="color: white; background-color: indianred;
                    float: right;
                    margin-bottom: 50px; width: 230px;"
                    data-balloon-pos="up"
                    data-balloon-length="large"
                    v-on:click="load_additional()"
                    aria-label="The more data you want to validate, the more reward you can get. But, be aware that once you receive new data, you can no longer fix previous data. And loading new data will take about 10~30 seconds.">
            <strong style="font-size: 22px;">Get more dataset</strong>
          </button>

          <div v-if="this.adding" class="loader" style="float: right; margin-top: -10px; margin-right: 30px;"/>
          <div v-if="this.adding" style="float: right; margin-right: 10px; font-size: 16px;"><strong>Uploading... Please wait. <br/>(about 10~30 seconds)</strong></div>

          <br/><br/><br/><br/><br/>

          <div v-if="this.submitting" style="float: right; font-size: 16px;"><strong>Submitting... Please wait. <br/>(about 10~30 seconds)</strong></div>
          <div v-if="this.submitting" class="loader" style="float: right; margin-top: -20px; margin-right: 20px; border-top: 20px solid blue"/>
          <br/><br/><br/>

          <button   type="button"
                    class="btn btn-lg"
                    :disabled="!this.all_answered || this.loading || this.submitting || this.adding || (this.answered_data.length===0 && this.add_times===0)"
                    style="color: white; padding: 10px; width: 1050px; background-color: slateblue;"
                    data-balloon-pos="up"
                    v-on:click="submitHit()"
                    aria-label="Please double-check that there are no blanks and unanswered questions. Only when all the number boxes have turned gray, you can submit the HIT">
            <strong style="font-size: 22px;">Submit</strong>
          </button>
          <br/>
        </div>
      </div>
      <br/><br/>

      <div>
        <form
            name='mturk_form'
            method='post'
            id='mturk-external-submit-form'
            v-bind:action="mturk_params.turkSubmitTo"
            type='hidden'
        >
          <input type='hidden' name='assignmentId' id='assignmentId' v-bind:value="mturk_params.assignmentId"/>
          <input type='hidden' name='bonusAmt' id='bonusAmt' v-bind:value="total_payment"/>
          <input type="hidden" name="generated_answers" v-model="generated_answers">
        </form>

        <div class="alert alert-danger" role="alert" v-if="show_submission_alert">
          There's an error in submission.
        </div>
      </div>
    </div>
  </div>
</template>


<script>
import Vue from 'vue';
import 'bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import queryString from "query-string";
import DataHandler from './get_csv.js'
import _ from 'lodash';
import 'balloon-css';
import Dataset from "./components/Dataset.vue";
import Payment from "./components/Payment.vue";
import Guideline from "./components/Guideline.vue";


document.title = "Validation Task"

export default {
  name: 'app',
  components: {
    'Dataset': Dataset,
    'Payment': Payment,
    'Guideline': Guideline,
  },

  data: function(){
    return {
        active_tab: "guideline",

        answered_data: [],
        stage: 0,
        number_of_data: 30,
        data_no: {},
        
        mturk_params: {
          assignmentId: "",
          hitId: "",
          turkSubmitTo: "",
          workerId: "",
          isPreview: true,
        },

        generated_answers: {    // JSON file 로 저장한 답안
          0: "",
          1: "",
          2: "",
          3: "",
          4: "",
          5: "",
          6: "",
          7: "",
          8: "",
          bonus: 0,
        },

        error_occurred: false,
        loading: false,
        submitting: false,
        adding: false,

        add_times: 0,

        show_submission_alert: false,
    }
  },

  filters: {          // apply common text formatting. be usable in two places: mustache interpolations and v-bind.
    currency: function(value){
      return value.toFixed(2);      // 소수점 아래 2자리까지만 표시.
    }
  },

  computed: {
    show_tab_guideline: function(){
      return this.active_tab === "guideline"
    },
    show_tab_payment: function(){
      return this.active_tab === "payment"
    },
    show_tab_workspace: function(){
      return this.active_tab === "workspace"
    },


    local_debug_mode: function(){       // flask backend database server ip?
      return this.host_name === "localhost"
      // return true
    },

    num_complete_page(){
      if (this.all_answered && !this.adding && !this.submitting && !this.loading && this.answered_data.length!==0){
        return parseInt(1 + this.add_times)
      }
      else if (this.adding){
        return parseInt(1 + this.add_times)
      }
      else if (this.submitting){
        if (this.answered_data.length !== 0){
          return parseInt(1+this.add_times)
        }
        else{
          return parseInt(this.add_times)
        }
      }
      else {
        if (this.answered_data.length===0 && this.add_times===0){
          return 0
        }
        else{
          return parseInt(this.add_times)
        }
      }
    },

    total_payment(){
      return this.num_complete_page * 2
    },

    all_answered(){
      _.each(this.answered_data, (sample) => {
        let item = Object.values(sample)[0]
        if (item.passed === true){
          item.all = true
        }
        else{
          item.all = item.object_bias1 !== null && item.object_bias2 !== null &&
              item.edit_distance1 !==null &&
              item.sure1 !== null && item.sure2 !== null
        }
      })

      return _.every(this.answered_data, (sample) => {
        return Object.values(sample)[0].all
      })
    },

  },

  mounted: async function(){
    const parsed_query_string = queryString.parse(window.location.search);
    console.log("parsed_query_string", parsed_query_string);
    this.host_name = window.location.hostname
    if (this.local_debug_mode){
      console.log("This is the local debug mode.")
    }

    let assignmentId = parsed_query_string.assignmentId;
    if(assignmentId === null || assignmentId === undefined){
      assignmentId = "TestAssignmentId";
    }
    if (assignmentId === "ASSIGNMENT_ID_NOT_AVAILABLE"){
      console.log("Preview mode.");
      this.mturk_params.isPreview = true;
    }

    const hitId = parsed_query_string.hitId;
    const turkSubmitTo = parsed_query_string.turkSubmitTo;
    let workerId = parsed_query_string.workerId;
    if(workerId === null || workerId === undefined || workerId === ""){
      workerId = "TestUser";
    }

    this.mturk_params.turkSubmitTo = turkSubmitTo + "/mturk/externalSubmit";
    this.mturk_params.workerId = workerId;
    this.mturk_params.hitId = hitId;
    this.mturk_params.assignmentId = assignmentId;

    this.active_tab = 'tab' in parsed_query_string ? parsed_query_string.tab : "guideline";
    console.log("mturk_params: ", this.mturk_params)


    this.loading = true
    let validation_data = await DataHandler.fetchData(this.number_of_data)
    this.loading = false
    // console.log("Total remaining validation_data: ", Object.keys(validation_data).length)
    await this.load_csv(validation_data);
  },

  methods: {
    switch_tab: function(tab){
      this.active_tab = tab;
    },

    shape_answer: function(samples_array){
      var results = {};
      for (let i=0; i<samples_array.length; i++){
        var sample = samples_array[i]       // {'i+1' : {'..': , '...': , ... }}
        sample = sample[i+1]              // dictionary = {'..': , '...': , ... }
        let val = 0
        if (this.object_bias1(sample) === 0 && this.object_bias2(sample) === 0 && this.edit_distance(sample) === 0 &&
            this.is_sure1(sample) === 1 && this.is_sure2(sample) === 1 && sample.passed === false){
          val = 1
        }
        results[sample['question_id']] = {
          'question': sample.question,
          'correct': sample.correct,
          'incorrect': sample.incorrect,
          'new_question': sample.new_question,
          'passed': sample.passed,
          'pass_reason': sample.pass_reason,
          'object_bias1': this.object_bias1(sample),
          'object_bias2': this.object_bias2(sample),
          'edit_distance': this.edit_distance(sample),
          'is_sure1': this.is_sure1(sample),
          'is_sure2': this.is_sure2(sample),
          'manager': this.mturk_params.workerId,
          'manage_aId': this.mturk_params.assignmentId,
          'valid': val
        }
      }
      return results
    },

    load_csv: function(pre_data){
      this.answered_data = [];
      this.data_no = {};
      var data = _.map(pre_data, (example) => {
        example.edit_distance1 = null;
        example.object_bias1 = null;
        example.object_bias2 = null;
        example.sure1 = null;
        example.sure2 = null;
        example.candidates = ['0', '1']
        example.all = false
        return example;
      });
      for(let i=0; i<data.length; i++ ){
        const temp = {};
        temp[i+1] = data[i]
        this.answered_data.push(temp)
      }

      this.data_no = Object.keys(this.answered_data)
      this.number_of_data = this.answered_data.length
      // console.log("answered_data:", this.answered_data)
      console.log("Complete to load", this.number_of_data, "Answered Data to validate.");
    },

    load_additional: async function(){
      this.adding = true;
      
      // save the previous data
      this.results = this.shape_answer(this.answered_data);           // make shaped this.results from this.creation_seeds
      this.generated_answers[this.add_times] = JSON.stringify(this.results)
      if (!this.local_debug_mode){
        await DataHandler.writeToS3(this.results, false)
      }

      // load new additional dataset
      let additional_data = await DataHandler.fetchData(this.number_of_data)
      await this.load_csv(additional_data);   // initialize this.creation_seeds

      this.stage = 0;
      this.adding = false;
      // document.getElementById("data_board").scrollIntoView({ behavior: 'smooth'});
      window.scrollTo(0,0)

      this.add_times += 1;
      // console.log(this.generated_answers)
    },

    move_to_num(idx){
      this.stage = parseInt(idx)
    },

    pre_data: function(){
      if (this.stage === 0){
        this.stage = parseInt(this.number_of_data)-1;
      }
      else{
        this.stage = parseInt(this.stage) - 1;
      }
      window.scrollTo(0,0)
    },

    next_data: function(){
      if (this.stage === this.number_of_data-1){
        this.stage = 0;
      }
      else{
        this.stage = parseInt(this.stage) + 1;
      }
      window.scrollTo(0,0)
    },

    object_bias1(item){
      if (parseInt(item.object_bias1) === 0){
        return 1
      }
      if (parseInt(item.object_bias1) === 1){
        return 0
      }
    },

    object_bias2(item){
      if (parseInt(item.object_bias2) === 0){
        return 1
      }
      if (parseInt(item.object_bias2) === 1){
        return 0
      }
    },

    edit_distance(item){
      if (parseInt(item.edit_distance1) === 0){      // 0 means "Yes", but I'd like to change that meaning to 1
        return 0
      }
      else{
        return 1
      }
    },

    is_sure1(item){
      if (parseInt(item.sure1) === 0){
        return 1
      }
      else{
        return 0
      }
    },


    is_sure2(item){
      if (parseInt(item.sure2) === 0){
        return 1
      }
      else{
        return 0
      }
    },


    submitHit: async function(){
      this.submitting = true;

      this.results = this.shape_answer(this.answered_data);           // make shaped this.results from this.creation_seeds
      this.generated_answers[this.add_times] = JSON.stringify(this.results)
      this.generated_answers['bonus'] = this.total_payment
      
      let bonus_list = {};
      let reward = {};
      reward[this.mturk_params.assignmentId] = this.total_payment;
      bonus_list[this.mturk_params.workerId] = reward

      // console.log("generated_answers: "  , this.generated_answers)
      // console.log(this.results)
      // console.log(Object.keys(this.results).length)
      if(!this.local_debug_mode){
        await DataHandler.writeToS3(this.results, bonus_list)

        Vue.nextTick(function () {
          document.getElementById('mturk-external-submit-form').submit();
        });
      }
      else{
        console.log("Local testing mode. Not submitting to mturk.");
        console.log("generated_answer: ", this.generated_answers);
      }

      this.submitting = false;
    },

  },
}
</script>

<style>
